import React from "react"
import { renderWithProviders } from "@rp/react/web/common/tools"
import { ErrorPage } from "./ErrorPage"

const mockHistoryPush = jest.fn()

jest.mock("react-router-dom", () => ({
  ...jest.requireActual("react-router-dom"),
  useNavigate: () => mockHistoryPush,
}))

const setup = () => {
  const utils = renderWithProviders(<ErrorPage />)
  return { ...utils }
}

describe("ErrorPage", () => {
  test("Render the page", () => {
    const { getByText } = setup()

    const oops = getByText(/Oops!/i)
    const something = getByText(/Something happened./i)
    const whatYouDo = getByText(/What would you like to do now?/i)

    expect(oops).toBeInTheDocument()
    expect(something).toBeInTheDocument()
    expect(whatYouDo).toBeInTheDocument()
  })
})
